import * as Yup from "yup";
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/

const BasicFormSchema = Yup.object().shape({
    userName: Yup.string()
    .min(2, "   - 3")
    .max(20, "   ")
    .required("  '"),
    phone: Yup.string()
    .matches(phoneRegExp, ': 0930000000')
    .min(7,': 0930000000')
    .max(19,': 0930000000')
    .required("   "),
    location: Yup.string()
    .min(3, "  ")
    .max(30, " ")
    .required("   "),
    description: Yup.string()
    .min(3, " ")
    .max(250, " ")
    .required("   "),
});

export default BasicFormSchema;